import { mapState, mapMutations } from 'vuex';
import tabbar from '@/common/tabbar.js';

export default {
	data() {
		return {
			roles: [
				{
					name: '混合方',
					value: 'role-1',
					selectedColor: '#036db7'
				},
				{
					name: '需求方',
					value: 'role-2',
					selectedColor: '#fa5276'
				},
				{
					name: '技能方',
					value: 'role-3',
					selectedColor: '#5ac792'
				}
			]
		};
	},
	computed: {
		...mapState(['role']),
		transRole() {
			return this.role;
		}
	},
	methods: {
		changRole(value) {
			this.update({
				key: 'role',
				value: value.value
			});

			tabbar.change(value.value);
		},

		goItem() {
			uni.navigateTo({
				url: '/pages/home/part/item?id=' + 10
			});
		},

		...mapMutations(['update'])
	}
};
